:root {
  --font-family: "Noto Sans JP", sans-serif;
  --color-black: #1b1b1b;
  --color-white: white; }

* {
  padding: 0;
  margin: 0; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  box-sizing: border-box;
  font-size: var(--font-family); }

html {
  font-size: 62.5%; }

.App {
  position: relative; }

.navbar {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: var(--color-black); }

.content {
  min-height: 80vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap; }

.navbar .navbar-header {
  color: var(--color-white);
  font-size: 2rem; }

.navbar .navbar-form {
  background-color: var(--color-white);
  flex: 0 0 100%;
  border: 0.2rem solid #ddd;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .navbar .navbar-form .navbar-input {
    font-family: inherit;
    border: none; }
    .navbar .navbar-form .navbar-input::-webkit-input-placeholder {
      font-family: inherit;
      font-size: 1.1rem; }
    .navbar .navbar-form .navbar-input:focus {
      outline: none; }
  .navbar .navbar-form .navbar-input-icon {
    width: 1rem;
    height: 1rem; }

.content-image-container {
  width: 20%;
  height: 20rem;
  margin: 1.5rem; }

.content-image {
  width: 100%;
  height: 100%; }

.model {
  background-color: rgba(0, 0, 0, 0.473);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 1000; }
  .model-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: inline-block; }
  .model-close-btn {
    position: absolute;
    color: blanchedalmond;
    font-size: 3rem;
    background-color: transparent;
    border: none;
    top: 1rem;
    right: 1rem; }
